import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  Typography,
  Box,
} from '@mui/material';
import { trackAppEvent } from '../services/analytics';

interface SignUpFormProps {
  open: boolean;
  onClose: () => void;
  type: 'personal' | 'team';
}

interface SignUpData {
  name: string;
  email: string;
  country: string;
  city: string;
  company?: string;
}

const countries = [
  'United States',
  'Canada',
  'United Kingdom',
  'Germany',
  'France',
  'Netherlands',
  'Sweden',
  'Norway',
  'Denmark',
  'Finland',
  'Switzerland',
  'Austria',
  'Belgium',
  'Italy',
  'Spain',
  'Portugal',
  'Poland',
  'Czech Republic',
  'Hungary',
  'Romania',
  'Bulgaria',
  'Greece',
  'Croatia',
  'Slovenia',
  'Slovakia',
  'Estonia',
  'Latvia',
  'Lithuania',
  'Ireland',
  'Iceland',
  'Luxembourg',
  'Malta',
  'Cyprus',
  'Australia',
  'New Zealand',
  'Japan',
  'South Korea',
  'China',
  'India',
  'Singapore',
  'Malaysia',
  'Thailand',
  'Vietnam',
  'Philippines',
  'Indonesia',
  'Taiwan',
  'Hong Kong',
  'Israel',
  'United Arab Emirates',
  'Saudi Arabia',
  'Qatar',
  'Kuwait',
  'Bahrain',
  'Oman',
  'Jordan',
  'Lebanon',
  'Egypt',
  'South Africa',
  'Nigeria',
  'Kenya',
  'Ghana',
  'Morocco',
  'Tunisia',
  'Algeria',
  'Brazil',
  'Argentina',
  'Chile',
  'Colombia',
  'Mexico',
  'Peru',
  'Uruguay',
  'Paraguay',
  'Ecuador',
  'Venezuela',
  'Bolivia',
  'Other',
];

const SignUpForm: React.FC<SignUpFormProps> = ({ open, onClose, type }) => {
  const [formData, setFormData] = useState<SignUpData>({
    name: '',
    email: '',
    country: '',
    city: '',
    company: '',
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const handleInputChange =
    (field: keyof SignUpData) =>
    (
      event:
        | React.ChangeEvent<HTMLInputElement>
        | { target: { value: unknown } }
    ) => {
      setFormData((prev) => ({
        ...prev,
        [field]: event.target.value as string,
      }));
    };

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    setIsSubmitting(true);
    setError(null);

    try {
      const response = await fetch(
        process.env.REACT_APP_API_ACCOUNTS_URL + '/codemapsAccounts/signup',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            ...formData,
            plan: type,
          }),
        }
      );

      if (!response.ok) {
        const data = await response.json();
        console.log(data);
        throw new Error(
          data.error ? data.error : 'Failed to submit sign-up form'
        );
      }

      setSuccess(true);
      trackAppEvent.signUp(type, formData.country);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
      trackAppEvent.error(
        'SignUp',
        err instanceof Error ? err.message : 'Unknown error'
      );
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleClose = () => {
    setError(null);
    setSuccess(false);
    setFormData({ name: '', email: '', country: '', city: '', company: '' });
    onClose();
  };

  const isValid =
    formData.name.trim() &&
    formData.email.trim() &&
    formData.country &&
    formData.city.trim() &&
    (type === 'personal' || formData.company?.trim());

  return (
    <Dialog open={open} onClose={onClose} maxWidth='sm' fullWidth>
      <DialogTitle>
        {type === 'personal'
          ? 'Activate Personal License'
          : 'Join Team Plan Waitlist'}
      </DialogTitle>
      <DialogContent>
        {success ? (
          <Box textAlign='center' py={4}>
            <Typography variant='h6' color='success.main' gutterBottom>
              Thank you for signing up!
            </Typography>
            <Typography variant='body2' color='text.secondary'>
              {type === 'personal'
                ? "We'll send you access instructions shortly."
                : "We'll notify you when team features are available."}
            </Typography>
          </Box>
        ) : (
          <Box component='form' onSubmit={handleSubmit} sx={{ mt: 1 }}>
            {error && (
              <Alert severity='error' sx={{ mb: 2 }}>
                {error}
              </Alert>
            )}

            <TextField
              autoFocus
              margin='dense'
              label='Full Name'
              type='text'
              fullWidth
              variant='outlined'
              value={formData.name}
              onChange={handleInputChange('name')}
              required
              sx={{ mb: 2 }}
            />

            <TextField
              margin='dense'
              label='Email Address'
              type='email'
              fullWidth
              variant='outlined'
              value={formData.email}
              onChange={handleInputChange('email')}
              required
              sx={{ mb: 2 }}
            />

            {type === 'team' && (
              <TextField
                margin='dense'
                label='Company Name'
                type='text'
                fullWidth
                variant='outlined'
                value={formData.company || ''}
                onChange={handleInputChange('company')}
                required
                sx={{ mb: 2 }}
              />
            )}

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Country</InputLabel>
              <Select
                value={formData.country}
                label='Country'
                onChange={handleInputChange('country')}
                required
              >
                {countries.map((country) => (
                  <MenuItem key={country} value={country}>
                    {country}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <TextField
              margin='dense'
              label='City'
              type='text'
              fullWidth
              variant='outlined'
              value={formData.city}
              onChange={handleInputChange('city')}
              required
              sx={{ mb: 2 }}
            />

            <Typography variant='caption' color='text.secondary'>
              We'll use this information to provide you with access and critical
              information about your subscription.
            </Typography>
          </Box>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} disabled={isSubmitting}>
          Cancel
        </Button>
        {!success && (
          <Button
            onClick={handleSubmit}
            variant='contained'
            disabled={!isValid || isSubmitting}
            startIcon={isSubmitting ? <CircularProgress size={16} /> : null}
          >
            {isSubmitting ? 'Submitting...' : 'Submit'}
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default SignUpForm;
